		width: 100%;
	}
	
	.container-login .overlay-left {
		-webkit-transform: translateX(-50%);
		        transform: translateX(-50%);
	}
	
	.container-login .container.right-panel-active .overlay-left {
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
	
	.container-login .overlay-right {
		right: 0;
		-webkit-transform: translateX(0);
		        transform: translateX(0);
	}
	
	.container-login .container.right-panel-active .overlay-right {
		-webkit-transform: translateX(50%);
		        transform: translateX(50%);
	}
 }
  
 /*untuk layar device berukuran sedang*/
 @media screen and (min-width: 550px) {
	.layout {
		position: relative;
		height: -webkit-fit-content;
		height: -moz-fit-content;
		height: fit-content;
	}
 }
  
 /*untuk layar device berukuran besar*/
 @media screen and (min-width: 800px) {
	.container {
		width: 800px;
		margin-left: auto;
		margin-right: auto;
	}
	.layout {
		position: relative;
		height: 100vh;
	}
	.container-login .responsive-btn, .responsive-span {
		display: none;
	}
	.container-login .overlay-container {
		display:-webkit-box;
	}
 }
